	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Easy Morning For Free Again!","date":"18 Jun 2009","slug":"/2009-06-18-easy-morning-for-free-again/","tags":"Y2009"},"html":"<p>We're <a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/06/HAN-Gratis-Fr&uuml;hstuck-WIN-18.06.09.pdf\" title=\"HAN Gratis-Fr&uuml;hstuck WIN - 18.06.09.pdf\">doing it again</a> next Tuesday, this time in Harburg. But PapaScott readers don't have to wait until the coupon appears in the newspaper, they can print out the coupon here and receive VIP treatment to boot! (We haven't yet decided what the VIP treatment will be...)</p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/06/papascott-coupon.jpg\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/06/papascott-coupon.jpg\" alt=\"papascott-coupon\" title=\"papascott-coupon\" width=\"450\" height=\"371\" class=\"alignnone size-full wp-image-3501\" /></a></p>\n<p><a href=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/06/HAN-Gratis-Fr&uuml;hstuck-WIN-18.06.09.pdf\" title=\"HAN Gratis-Fr&uuml;hstuck WIN - 18.06.09.pdf\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/06/HAN-Gratis-Fruehstuck-WIN-18.06.09.jpg\" alt=\"HAN Gratis-Fruehstuck WIN - 18.06.09.jpg\" border=\"0\" width=\"450\" height=\"563\" /></a></p>"},"next":{"fields":{"title":"Anatomical Drawing","date":"13 Jun 2009","slug":"/2009-06-13-anatomical-drawing/"}},"previous":{"fields":{"title":"Interesting Drive","date":"27 Jun 2009","slug":"/2009-06-27-interesting-drive/"}}},{"node":{"fields":{"title":"Anatomical Drawing","date":"13 Jun 2009","slug":"/2009-06-13-anatomical-drawing/","tags":"Y2009"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3621818150\" title=\"View 'IMG_0170.JPG' on Flickr.com\"><img src=\"http://farm4.static.flickr.com/3332/3621818150_2a3fb4d073.jpg\" alt=\"IMG_0170.JPG\" border=\"0\" width=\"500\" height=\"375\" /></a></p>\n<p>Christopher has been busy learning about and drawing whales. This morning he taped together several sheets of paper and started drawing an internal view of a sperm whale with skeleton and organs. Looks like he'll need more paper to finish.</p>"},"next":{"fields":{"title":"Christopher's Platform","date":"09 Jun 2009","slug":"/2009-06-09-christophers-platform/"}},"previous":{"fields":{"title":"Easy Morning For Free Again!","date":"18 Jun 2009","slug":"/2009-06-18-easy-morning-for-free-again/"}}},{"node":{"fields":{"title":"Christopher's Platform","date":"09 Jun 2009","slug":"/2009-06-09-christophers-platform/","tags":"Y2009"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/3611248234\" title=\"View 'IMG_0164' on Flickr.com\"><img src=\"https://farm4.static.flickr.com/3313/3611248234_6ca0540c2e_s.jpg\" alt=\"IMG_0164\" border=\"0\" width=\"75\" height=\"75\" align=\"right\" /></a>All the news about elections (European and upcoming German) reminded Christopher of two things. One, because of his dual citizenship, he can vote twice as often as either of his parents. Secondly, if he were to run for German Chancellor*, he would campaign for the following.</p>\n<ol>\n<li>More schools</li>\n<li>More help for victims of natural disaster, and research to prevent such disasters</li>\n<li>Energy conservation, less oil and coal, more wind and solar</li>\n<li>Abolish war</li>\n</ol>\n<p>Sounds like a solid platform, but the devil is going to be in the details.</p>\n<p>*Yes, we realize he previously <a href=\"http://www.papascott.de/archives/2008/11/05/yes-we-can-in-2036/\">announced his candidacy for the US Presidency</a>. We'll issue a statement when we know which is correct.</p>"},"next":{"fields":{"title":"Pesky Parliament","date":"08 Jun 2009","slug":"/2009-06-08-pesky-parliament/"}},"previous":{"fields":{"title":"Anatomical Drawing","date":"13 Jun 2009","slug":"/2009-06-13-anatomical-drawing/"}}}],"pathPrefix":"","first":false,"last":false,"index":56,"pageCount":1101}}

